<template>
  <div class="detail">
    <van-nav-bar title="商品详情" left-text="返回" left-arrow fixed @click-left="back()" />
    <div class="detail-img-box">
      <div class="img-box">
        <img class="auto-img" :src="product.large_img" alt />
      </div>
      <div class="img-content">
        <div class="name">{{product.name}}</div>
        <div class="enname">{{product.enname}}</div>
      </div>
    </div>
    <div class="detail-box">
      <div class="detail-spec" v-for="(item,index) in product.specs" :key="index">
        <div class="spec-title">{{item.title}}</div>
        <div class="spec-content clearfix">
          <div
            class="spec-desc fl"
            :class="{active:value.isActive}"
            v-for="(value,i) in item.specs"
            :key="i"
            @click="switchSpec(value, index)"
          >{{value.title}}</div>
        </div>
      </div>
      <van-divider :style="{borderTopWidth:'1px',borderColor:'#eeeeee'}" />
      <div class="detail-mes">
        <div class="detail-content-t">商品描述</div>
        <div
          class="detail-content"
          v-for="(item,index) in product.desc"
          :key="index"
        >{{index+1}}、{{item}}</div>
      </div>
      <div>
        <van-divider :style="{borderTopWidth:'1px',borderColor:'#eeeeee'}" />
      </div>
      <div class="detaile-price clearfix">
        <div class="price fl">￥{{product.price}}</div>
        <div class="numble fr">
          <van-stepper theme="round" disable-input button-size="20" />
        </div>
      </div>
    </div>
    <van-goods-action>
      <van-goods-action-icon
        icon="bag"
        text="购物袋"
        :badge="shopBagCount==0 ? '': shopBagCount"
        color="#989898"
        @click="goShoppingBag()"
      />
      <van-goods-action-icon
        icon="like"
        text="喜欢"
        :color="isLike ? '#3585f9' : '#989898'"
        @click="likeProducts()"
      />
      <van-goods-action-button text="加入购物袋" color="#b4d4ff" @click="addShoppingBag()" />
      <van-goods-action-button text="立即购买" color="#3585f9" @click="buyNow()" />
    </van-goods-action>
  </div>
</template>

<script>
import "../assets/less/detail.less";
import { utils } from "../assets/js/utils";
export default {
  name: "Detail",
  data() {
    return {
      pid: "",
      product: {},
      count: 1,
      shopBagCount: 0,
      price: 0,
      isLike: false,
    };
  },
  created() {
    this.pid = this.$route.query.pid;

    this.getProductsByPid();
    this.findLikeProducts();
    this.findShoppingBag();
  },
  methods: {
    //根据传过来的商品id得到商品详情信息
    getProductsByPid() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/productDetail",
        params: {
          appkey: this.appkey,
          pid: this.pid,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 600) {
            //处理商品描述数据
            result.data.result[0].desc = result.data.result[0].desc.split(/\n/);

            //处理商品规格 温度 : 冷/热 ==> 冷 热
            let specsItem = ["cream", "milk", "sugar", "tem"];
            let specsData = [];

            specsItem.map((v) => {
              if (result.data.result[0][v] == "") {
                return;
              }
              let spec = {
                title: result.data.result[0][v + "_desc"],
                specs: [],
              };

              //处理冷热等
              let classifyItem = result.data.result[0][v].split("/");

              classifyItem.map((element, index) => {
                let classifyData = {
                  title: element,
                  isActive: index == 0,
                };
                spec.specs.push(classifyData);
              });

              specsData.push(spec);
            });

            result.data.result[0].specs = specsData;
            this.product = result.data.result[0];

            this.price = this.product.price;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //切换商品规格，加糖不加糖等
    switchSpec(value, index) {
      if (value.isActive) {
        return;
      }
      let specsData = this.product.specs[index].specs;
      for (let i = 0; i < specsData.length; i++) {
        if (specsData[i].isActive) {
          specsData[i].isActive = false;
          break;
        }
      }
      value.isActive = true;
    },
    back() {
      this.$router.go(-1);
    },
    goShoppingBag() {
      this.$router.push({ name: "ShoppingBag" });
    },
    //收藏/取消收藏商品
    likeProducts() {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        return this.$router.push({ name: "Login" });
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      let data = {
        appkey: this.appkey,
        pid: this.pid,
        tokenString: localStorage.getItem("WTPTK"),
      };
      data = utils.queryString(data);
      this.axios({
        method: "POST",
        url: this.isLike ? "/notlike" : "/like",
        data,
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 800) {
            this.$toast(result.data.msg);
            this.isLike = true;
          }
          if (result.data.code == 900) {
            this.$toast(result.data.msg);
            this.isLike = false;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //查找收藏的商品
    findLikeProducts() {
      let tokenString = localStorage.getItem("WTPTK");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/findlike",
        params: {
          appkey: this.appkey,
          pid: this.pid,
          tokenString: tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 1000) {
            if (result.data.result.length == 1) {
              this.isLike = true;
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //添加到购物袋
    addShoppingBag(isBuyNow) {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      //获取到选择的规格
      let rules = this.product.specs;
      let rulesData = [];
      rules.map((v) => {
        for (let i = 0; i < v.specs.length; i++) {
          if (v.specs[i].isActive) {
            rulesData.push(v.specs[i].title);
            break;
          }
        }
      });

      let data = {
        pid: this.pid,
        count: this.count,
        rule: rulesData.join("/"),
        appkey: this.appkey,
        tokenString: localStorage.getItem("WTPTK"),
      };
      data = utils.queryString(data);
      this.axios({
        method: "POST",
        url: "/addShopcart",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 3000) {
            if (result.data.status == 1) {
              this.shopBagCount++;
            }

            //立即购买
            if (isBuyNow == true) {
              setTimeout(() => {
                this.$router.push({
                  name: "SubmitOrder",
                  query: { sids: result.data.sid },
                });
              }, 300);
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //查找购物袋的数目
    findShoppingBag() {
      let tokenString = localStorage.getItem("WTPTK");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString: tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 5000) {
            this.shopBagCount = result.data.result.length;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //立即购买
    buyNow() {
      this.addShoppingBag(true);
    },
  },
};
</script>

<style lang="less" scoped>
</style>